import Highcharts from 'highcharts';
import Exporting from 'highcharts/modules/exporting';
import moment from 'moment-timezone';
const $ = jQuery;

export default (firstDate, lastDate, itemsSelected) => {

    const eventGraphicColumNewSummary = () => {
        /**
         * Get values
         */
        var arr_question_bar = [];
        var arr_option_list = [];
        var is_legend = true;
        var title_char = "";
        var arr_option_text_global = "";
        var arr_categories_char = [];
        var color = [];
        var color_index = 0;
        var into = false;
        if (ramp_daily_data && ramp_daily_data.length != 0
            && $("#result_score_lifechart_sysmptoms_summary").length != 0) {
            ramp_daily_data.forEach(function (ramp_daily, index_ramp_daily, array) {
                if(ramp_daily.charts) {
                    ramp_daily.charts.forEach(function (item, index_char, array) {

                        if(itemsSelected){
                            if(itemsSelected.includes(ramp_daily.form_id)){
                              into = true;
                            }else{
                              into = false;
                            }
                        }else{
                            into = true;
                        }

                        if (into && item['type'] == 'barnew') {
                            arr_question_bar = item['questions'];
                            arr_option_list = item['options_list'];
                            title_char = ramp_daily.form_name;

                            var user_responses = ramp_daily.user_responses;
                            var data_form = ramp_daily.data_form;
                            /**
                             * Valid legend
                             */
                            if (item['legend'] == 'no') {
                                is_legend = false;
                            } else {
                                is_legend = true;
                            }

                            /**
                             * Get week days, star first week Monday
                             */
                            var arr_categories = [];
                            var dateNow = '';
                            var dateNowChar = '';
                            for (var i = 0; i < 7; i++) {
                                dateNow = moment(firstDate, "MM-DD-YYYY").add(i, 'd').format('MM-DD-YYYY');
                                arr_categories[i] = dateNow;
                                dateNowChar = moment(firstDate, "MM-DD-YYYY").add(i, 'd').format('dddd');
                                arr_categories_char[i] = dateNowChar;
                            }

                            /**
                             * Valid sum values
                             */
                            arr_option_list = [];
                            arr_option_list[0] = ramp_daily.form_name;//add only option in char

                            /**
                             * get value by day of week
                             */
                            var arr_question_answer = [];
                            var suma_date = 0;
                            var count_semana = 0;
                            // For Question Summart response user
                            for (const option in arr_option_list) {
                                arr_question_answer[arr_option_list[option]] = [];
                                suma_date = 0;
                                count_semana = 0;
                                for (var date in arr_categories.sort()) {
                                    suma_date = 0;
                                    date = arr_categories[date];
                                    for (const question in arr_question_bar) {
                                        for (const answer in user_responses['responses'][date]) {
                                            if (answer == arr_question_bar[question]) {

                                                if (arr_option_list[option] == 'count-more') {
                                                    suma_date = ramp_daily.user_responses['responses'][date][answer].length;
                                                } else if (arr_option_list[option] == ramp_daily.form_name) {
                                                    var num = parseInt(ramp_daily.user_responses['responses'][date][answer],10);
                                                    if(!isNaN(num)){
                                                        suma_date = suma_date + Number(ramp_daily.user_responses['responses'][date][answer]); // get max value
                                                    }
                                                } else {
                                                    if (ramp_daily.user_responses['responses'][date][answer].toString() == option.toString()) {
                                                        suma_date++; // get count the times they appear
                                                    }
                                                }

                                            }
                                        }
                                    }
                                    arr_question_answer[arr_option_list[option]][count_semana] = suma_date;
                                    count_semana++;
                                }
                            }

                            /**
                             * Get array text highchart
                             */
                            var arr_option_text = "";
                            color[0] = "#2094e7";
                            color[1] = "#297d85";
                            color[2] = "#e32f35";
                            color[3] = "#1c2547";
                            color[4] = "#1d071c";
                            var count = 0;
                            var data = '';
                            for (let answer in arr_question_answer) {
                                data = arr_question_answer[answer];
                                if(item['questions'].length > 1){
                                    answer = ramp_daily.form_name;
                                }

                                if (count == 0) {
                                    arr_option_text = arr_option_text + "{name: '" + answer + "', data: [" + data + "], color: '" + color[color_index] + "'},";
                                } else if (count == 1) {
                                    arr_option_text = arr_option_text + "{name: '" + answer + "', data: [" + data + "], color: '" + color[color_index] + "'},";
                                } else if (count == 2) {
                                    arr_option_text = arr_option_text + "{name: '" + answer + "', data: [" + data + "], color: '" + color[color_index] + "'},";
                                } else if (count == 3) {
                                    arr_option_text = arr_option_text + "{name: '" + answer + "', data: [" + data + "], color: '" + color[color_index] + "'},";
                                } else if (count == 4) {
                                    arr_option_text = arr_option_text + "{name: '" + answer + "', data: [" + data + "], color: '" + color[color_index] + "'},";
                                } else {
                                    arr_option_text = arr_option_text + "{name: '" + answer + "', data: [" + data + "]},";
                                }
                                count++;
                                color_index++;
                            }
                            
                            arr_option_text_global = arr_option_text_global + arr_option_text;
                        }
                    });
                }
            });

            /**
             * Chart Summary
             */

            Highcharts.chart('result_score_lifechart_sysmptoms_summary', {
                chart: {
                    type: 'column',
                    backgroundColor: "transparent"

                },
                title: {
                    text: ''
                },
                xAxis: {
                    categories: arr_categories_char
                },
                yAxis: {
                    min: 0,
                    title: {
                        text: 'Symptoms Felt'
                    },
                    labels: {
                        enabled:false,
                    }
                },
                legend: {
                    align: 'center',
                    x: 10
                },
                tooltip: {
                    headerFormat: '<b>{point.x}</b><br/>',
                    pointFormat: '{series.name}: {point.y}<br/>Total: {point.stackTotal}'
                },
                plotOptions: {
                    column: {
                        stacking: '',
                        dataLabels: {
                            enabled: false
                        },
                        pointPadding: 0.2,
                        borderWidth: 0,
                        showInLegend: true,
                    }
                },
                series: eval("[" + arr_option_text_global + "]"),
                credits: {
                    enabled: false
                },
            });

        }
    };

    eventGraphicColumNewSummary();
}
