import initGraphic from './graphic/index';
import displayGraphic from './displayGraphic.js';
import initModals from './infoModal.js';

function init() {
  if (RAMP_LIFECHART.post_type === 'ramp_lifechart') {
    try {
      initGraphic();
      displayGraphic();
    } catch (error) {
      console.log('error', error);
    }
  }
}

function ready(fn) {
  if (
    document.attachEvent
      ? document.readyState === "complete"
      : document.readyState !== "loading"
  ) {
    fn();
  } else {
    document.addEventListener("DOMContentLoaded", fn);
  }
}

ready(() => {
  init();
  initModals();
});