const $ = jQuery;
const d = console.log;

export default () => {
  /**
   * Open modal lifechart
   * @since Input Health Options v0.0.3
   */
  const initModalLifechart = () => {
    console.log('initModallifechart');
    $('.active #modal-lifechart').css('display', 'block');

    const $cancelLifechartButton = $('#modal-lifechart-cancel');
    const $showLifechartButtonSymp = $('.icon_img_symp');
    const $showLifechartButtonAssem = $('.icon_img_ass');
    const $showLifechartButtonOver = $('.icon_img_ov');

    $cancelLifechartButton.on('click', function (event) {
      $('#modal-lifechart').css("display", "none");
    });

    $showLifechartButtonSymp.on('click', function (event) {
      $('#modal-lifechart').css("display", "block");
    });

    $showLifechartButtonAssem.on('click', function (event) {
      $('#modal-lifechart').css("display", "block");
    });

    $showLifechartButtonOver.on('click', function (event) {
      $('#modal-lifechart').css("display", "block");
    });

  };

  initModalLifechart();

};